import 'bootstrap/dist/css/bootstrap.min.css';
import $ from 'jquery';

const passcode = '1234';
const targetDate = new Date('2023-05-17T22:10:00');

$(document).ready(() => {
  const countdownElement = $('#countdown');
  const mainPageElement = $('#mainPage');
  const passcodePrompt = $('#passcodePrompt');
  const passcodeInput = $('#passcodeInput');
  const submitPasscode = $('#submitPasscode');

  mainPageElement.hide(); // Hide main page initially
  passcodePrompt.hide(); // Hide passcode prompt initially

  startCountdown();

  function startCountdown() {
    const intervalId = setInterval(() => {
      const currentTime = new Date();
      const difference = targetDate - currentTime;

      if (difference <= 0) {
        clearInterval(intervalId);
        countdownElement.hide(1000);
        // countdownElement.hide();
        // passcodePrompt.show();
        passcodePrompt.show(2000);
        // passcodePrompt[0].scrollIntoView({ behavior: "smooth" }); // Smooth scroll to passcodePrompt
      } else {
        const seconds = Math.floor((difference / 1000) % 60);
        const minutes = Math.floor((difference / 1000 / 60) % 60);
        const hours = Math.floor((difference / (1000 * 60 * 60)) % 24);
        const days = Math.floor(difference / (1000 * 60 * 60 * 24));

        countdownElement.text(`Countdown: ${days} days, ${hours} hours, ${minutes} minutes, ${seconds} seconds`);
      }
    }, 1000);
  }

  submitPasscode.on('click', () => {
    if (passcodeInput.val() === passcode) {
      passcodePrompt.hide();
      mainPageElement.show();
      mainPageElement[0].scrollIntoView({ behavior: "smooth" }); // Smooth scroll to mainPage
    } else {
      alert('Wrong passcode!');
    }
  });
});
